r\n  }\r\n\r\n  .col {\r\n    float: left;\r\n    box-sizing: border-box;\r\n    padding: 0 $gutter-width / 2;\r\n    min-height: 1px;\r\n\r\n    &[class*=\"push-\"],\r\n    &[class*=\"pull-\"] {\r\n      position: relative;\r\n    }\r\n\r\n    $i: 1;\r\n    @while $i <= $num-cols {\r\n      $perc: unquote((100 / ($num-cols / $i)) + \"%\");\r\n      &.s#{$i} {\r\n        width: $perc;\r\n        @include reset-offset;\r\n      }\r\n      $i: $i + 1;\r\n    }\r\n\r\n    $i: 1;\r\n    @while $i <= $num-cols {\r\n      $perc: unquote((100 / ($num-cols / $i)) + \"%\");\r\n      @include grid-classes(\"s\", $i, $perc);\r\n      $i: $i + 1;\r\n    }\r\n\r\n    @media #{$medium-and-up} {\r\n\r\n      $i: 1;\r\n      @while $i <= $num-cols {\r\n        $perc: unquote((100 / ($num-cols / $i)) + \"%\");\r\n        &.m#{$i} {\r\n          width: $perc;\r\n          @include reset-offset;\r\n        }\r\n        $i: $i + 1\r\n      }\r\n\r\n      $i: 1;\r\n      @while $i <= $num-cols {\r\n        $perc: unquote((100 / ($num-cols / $i)) + \"%\");\r\n        @include grid-classes(\"m\", $i, $perc);\r\n        $i: $i + 1;\r\n      }\r\n    }\r\n\r\n    @media #{$large-and-up} {\r\n\r\n      $i: 1;\r\n      @while $i <= $num-cols {\r\n        $perc: unquote((100 / ($num-cols / $i)) + \"%\");\r\n        &.l#{$i} {\r\n          width: $perc;\r\n          @include reset-offset;\r\n        }\r\n        $i: $i + 1;\r\n      }\r\n\r\n      $i: 1;\r\n      @while $i <= $num-cols {\r\n        $perc: unquote((100 / ($num-cols / $i)) + \"%\");\r\n        @include grid-classes(\"l\", $i, $perc);\r\n        $i: $i + 1;\r\n      }\r\n    }\r\n\r\n    @media #{$extra-large-and-up} {\r\n\r\n      $i: 1;\r\n      @while $i <= $num-cols {\r\n        $perc: unquote((100 / ($num-cols / $i)) + \"%\");\r\n        &.xl#{$i} {\r\n          width: $perc;\r\n          @include reset-offset;\r\n        }\r\n        $i: $i + 1;\r\n      }\r\n\r\n      $i: 1;\r\n      @while $i <= $num-cols {\r\n        $per